.Player {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}
.Player.reversed {
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
}

.Player.reversed .lifeDisplay {
    -webkit-transform: scale(-1, -1);
        -ms-transform: scale(-1, -1);
            transform: scale(-1, -1);
}

.lifeDisplay {
    font-size: 20vh;
    font-weight: 600;
    width: 150px;
}

.lifeChangeEventBubble {
    position: absolute;
    font-size: 6vh;
    font-weight: 600;
    color: white;
    border-radius: 4vh;
    padding: 2vh;
}

.lifeChangeEventBubble.show {
    opacity: 1;
}

.lifeChangeEventBubble.hide {
    opacity: 0;
}

.lifeChangeEventBubble.fadeOut {
    opacity: 0;
    -webkit-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s;
}

.lifeChangeEventBubble.positive {
    background-color: lightgreen;
}

.lifeChangeEventBubble.negative {
    background-color: hotpink;
}

.Player:not(.reversed) .lifeChangeEventBubble {
    top: 0px;
    right: 11vh;
}

.Player.reversed .lifeChangeEventBubble {
    -webkit-transform: scale(-1, -1);
        -ms-transform: scale(-1, -1);
            transform: scale(-1, -1);
    bottom: 0px;
    left: 11vh;
}



